.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  min-height: 100vh;
  justify-content: center;
  color: black;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-grid-container {
  display: grid;
  gap: 20px;
  grid-template-columns: 20% 20% 20% 20%;
  padding: 10px;
  justify-content: center;
}

.App-grid-item {
  border: 5px solid rgba(0, 0, 0, 0.8);
  padding: 20px;
  font-size: 1rem;
  text-align: left;
  border-radius: 10px;
  box-shadow: 5px 5px 10px 3px gray;
  cursor: pointer;
}

.App-grid-item:hover {
  box-shadow: none;
}
